<template>
  <div id="app" class="dark">
    <div class="loginwrapper" v-if="!connected">
        <button  @click="connect">Anslut</button>
        <input type="text" v-model="game.userName" />
    </div>

    <score-board :game="game" @markScore="e => markScore(e)" />
     <div class="dicewrapper">
        <dice :dice="game.dice" :canHold="rollsLeft < 3 && rollsLeft > 0" @diceholdchanged="notifyDiceHoldChanged" />
        <button class="rollbutton" v-if="connected && gameStarted && myTurn && rollsLeft > 0" @click="roll">Roll({{rollsLeft}})</button>
    </div>
    
    <div class="logoutwrapper">
      <button v-if="connected && !gameStarted" @click="startGame">Start Game</button>
      <button v-else-if="connected" @click="leaveGame">End Game</button>
    </div>
    
  </div>
</template>

<script>
import ScoreBoard from "./components/ScoreBoard.vue";
import Dice from "./components/Dice.vue";
export default {
  name: "App",
  components: {
    ScoreBoard,
    Dice,
  },
  data() {
    return {
      connected: false,
      gameStarted:false,
      game: {        
        userName:'',
        players: [],

        dice: [
          { value: -1, isHeld: false },
          { value: -1, isHeld: false },
          { value: -1, isHeld: false },
          { value: -1, isHeld: false },
          { value: -1, isHeld: false },
        ],
      },
      socket: null,
      wsUri: "wss://rancken.dyndns.org/websocket/websession",
    };
  },
  mounted() {
    // this.socket = this.createSocket();
  },
  methods: {
    connect() {
      this.socket = this.createSocket();
    },
    markScore(e) {
      if(this.myTurn) {
        console.log("markScore");
        console.log(e);
        this.sendCommand(
          JSON.stringify({ command: "score", args: this.game.userName + ";" + e.markAs })
        );
      }
    },
    
    createSocket() {
      var websocket = new WebSocket(this.wsUri);
      websocket.onopen = () => {
        this.sendCommand(
          JSON.stringify({ command: "hello", args: this.game.userName })
        );
        this.connected = true;
      };

      websocket.onclose = () => {
        this.connected = false;
      };

      websocket.onmessage = (e) => {
        let ev = JSON.parse(e.data);
        let i = 0;
        let player = null;
        let diceStates = null;
        switch (ev.event) {
          case "UserJoined":
            this.game.players.push({
              userName: ev.player,
              isHisTurn: false,
              score: this.emptyScore,
              rollsLeft:3
            });
            break;
          case "UserLeft":
            i = this.game.players.findIndex((p) => p.userName == ev.player);
            console.log(ev.player + " left. His index is " + i);

            if (i >= 0) {
              /* It was I who quit */
              if(this.game.players[i].userName === this.game.userName) {
                this.socket.close();
                this.connected = false;      
                this.gameStarted = false;         
              }
              console.log('splicing at index ' + i);
              this.game.players.splice(i, 1);
            }
            
            
            break;

          case "UserNameChanged":
            this.game.userName = ev.player;
            player = this.game.players.find((p) => p.userName == ev.player);
            if (player) {
              player.userName = ev.player;
            }
            break;

            case "UserChangedDiceHold":
              diceStates = ev.eventArg;
              if(diceStates.length === 5) {
                for(let d=0;d<5;d++) {
                  this.game.dice[d].isHeld = diceStates[d] === 1;
                }
              }
            break;

          case "TurnChanged":
            this.gameStarted = true;
            this.game.players.forEach((p) => {
              p.isHisTurn = false;
            });
            player = this.game.players.find((p) => p.userName == ev.player);
            if (player) {
              player.isHisTurn = true;
              player.rollsLeft = 3;
            } 
            break;

          case "UserRolled":
            player = this.game.players.find((p) => p.userName == ev.player);
            this.game.dice[0].value = ev.eventArg.d1.value;
            this.game.dice[1].value = ev.eventArg.d2.value;
            this.game.dice[2].value = ev.eventArg.d3.value;
            this.game.dice[3].value = ev.eventArg.d4.value;
            this.game.dice[4].value = ev.eventArg.d5.value;
            player.rollsLeft = ev.eventArg.rollsLeft;
            break;

           case "UserScored":
            player = this.game.players.find((p) => p.userName == ev.player);
            if(player)
              player.score = ev.eventArg;
            this.game.dice.forEach((d) => {
              d.isHeld = false;
             });
            
            break;

            
        }
      };

      websocket.onerror = function (e) {
        console.log("ERROR: " + e.data);
      };
      return websocket;
    },

    roll() {
      
      this.startShuffle(0,7);
    },

    notifyDiceHoldChanged() {
      console.log("---- dice hold changed ----");
      let diceData = [];
      for(let d=0;d<5;d++) {
         diceData.push(this.game.dice[d].isHeld ? 1 : 0);
      }
      this.sendCommand(
          JSON.stringify({
            command: "changeDiceHold",
            args: this.game.userName + ";" + JSON.stringify(diceData),
          })
        );
    },

    startShuffle(rep, reps) {
      let rollData = "";

      rollData += this.game.dice[0].isHeld ? "1," : "0,";
      rollData += this.game.dice[1].isHeld ? "1," : "0,";
      rollData += this.game.dice[2].isHeld ? "1," : "0,";
      rollData += this.game.dice[3].isHeld ? "1," : "0,";
      rollData += this.game.dice[4].isHeld ? "1," : "0,";
      
      if(rep === reps) {
       this.sendCommand(
          JSON.stringify({
            command: "roll",
            args: this.game.userName + ";" + rollData,
          })
        );
      }
      else {
        this.game.dice.forEach((d) => {
              if(!d.isHeld)
                d.value = Math.round(Math.random() * 5) + 1;
            });
         window.setTimeout(() => this.startShuffle(rep+1,reps),200);
         
        
      }

    },

    startGame() {
      this.sendCommand(JSON.stringify({ command: "startgame", args: this.game.userName }));
      this.gameStarted = true;
    },

    leaveGame() {
      this.sendCommand(JSON.stringify({ command: "goodbye", args: this.game.userName }));
      this.gameStarted = true;
    },
     
    sendCommand(command) {
      if (this.socket.readyState === 1) {
        this.socket.send(command);
      } else {
        console.log("SOCKET IS CLOSED. RECONNECTING");
        this.socket = this.createSocket();
        this.socket.send(command);
      }
    },
  },

  computed: {
    emptyScore() {
      return {
        ones: -1,
        twos: -1,
        threes: -1,
        fours: -1,
        fives: -1,
        sixes: -1,
        onepair:-1,
        twopair:-1,
        tripple:-1,
        quadruple:-1,
        smallstrait:-1,
        largestrait:-1,
        fullhouse:-1,
        chance:-1,
        yatzy:-1
      };
    },

     me() {
      return this.game.players.find((p) => p.userName == this.game.userName);
    },
    myTurn() {
      let me = this.game.players.find((p) => p.userName == this.game.userName);
      if (me && me.isHisTurn) return true;
      return false;
    },
    rollsLeft() {
      let me = this.game.players.find((p) => p.userName == this.game.userName);
      if (me) return me.rollsLeft;
      return 0;
    },
  },
};
</script>

