<template>
  <div class="scoreboard">
    <div class="minor-scale">
      <div class="column head">
          <div class="cell">-</div>        
          <div class="cell">Ettor</div>
          <div class="cell">Tvåor</div>
          <div class="cell">Treor</div>
          <div class="cell">Fyror</div>
          <div class="cell">Femmor</div>
          <div class="cell">Sexor</div>
          <div class="cell separator">Summa</div>
          <div class="cell">Bonus</div>
          <div class="cell">1 par</div>
          <div class="cell">2 par</div>
          <div class="cell">Tretal</div>
          <div class="cell">Fyrtal</div>
          <div class="cell">L. stege</div>
          <div class="cell">S. stege</div>
          <div class="cell">Kåk</div>
          <div class="cell">Chans</div>
          <div class="cell">YATZY</div>
           <div class="cell separator">Summa</div>

      </div>

      <div class="column" v-for="(player) in game.players" :key="player.userName" >
          <div class="cell" v-html="player.userName"></div>
          <div class="cell" @click="markScore(player, 'ones')" v-html="displayScore(player,1)"></div>
          <div class="cell" @click="markScore(player,'twos')" v-html="displayScore(player,2)"></div>
          <div class="cell" @click="markScore(player,'threes')" v-html="displayScore(player,3)"></div>
          <div class="cell" @click="markScore(player,'fours')" v-html="displayScore(player,4)"></div>
          <div class="cell" @click="markScore(player,'fives')" v-html="displayScore(player,5)"></div>
          <div class="cell" @click="markScore(player,'sixes')" v-html="displayScore(player,6)"></div>
          <div class="cell separator" v-html="getMinorSum(player)"></div>
          <div class="cell" v-html="getBonus(player) || '&nbsp;'"></div>
          <div class="cell" @click="markScore(player,'onepair')" v-html="displayScore(player,7)"></div>
          <div class="cell" @click="markScore(player,'twopair')" v-html="displayScore(player,8)"></div>
          <div class="cell" @click="markScore(player,'tripple')" v-html="displayScore(player,9)"></div>
          <div class="cell" @click="markScore(player,'quadruple')" v-html="displayScore(player,10)"></div>
          <div class="cell" @click="markScore(player,'smallstrait')" v-html="displayScore(player,11)"></div>
          <div class="cell" @click="markScore(player,'largestrait')" v-html="displayScore(player,12)"></div>
          <div class="cell" @click="markScore(player,'fullhouse')" v-html="displayScore(player,13)"></div>
          <div class="cell" @click="markScore(player,'chance')" v-html="displayScore(player,14)"></div>
          <div class="cell" @click="markScore(player,'yatzy')" v-html="displayScore(player,15)"></div>
          <div class="cell separator" v-html="getFullSum(player)"></div>
          
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ScoreBoard',
  props: {
    game: Object
  },
  methods: {
    displayScore(player,idx) {
      switch(idx) {
        case 1:
          return player.score.ones == -1 ? ' ' : player.score.ones.toString();
        case 2:
          return player.score.twos == -1 ? ' ' : player.score.twos.toString();
        case 3:
          return player.score.threes == -1 ? ' ' : player.score.threes.toString();
       case 4:
          return player.score.fours == -1 ? ' ' : player.score.fours.toString();
       case 5:
          return player.score.fives == -1 ? ' ' : player.score.fives.toString();
       case 6:
          return player.score.sixes == -1 ? ' ' : player.score.sixes.toString();
      case 7:
          return player.score.onepair == -1 ? ' ' : player.score.onepair.toString();
      case 8:
          return player.score.twopair == -1 ? ' ' : player.score.twopair.toString();
      case 9:
          return player.score.tripple == -1 ? ' ' : player.score.tripple.toString();
      case 10:
          return player.score.quadruple == -1 ? ' ' : player.score.quadruple.toString();
      case 11:
          return player.score.smallstrait == -1 ? ' ' : player.score.smallstrait.toString();
      case 12:
          return player.score.largestrait == -1 ? ' ' : player.score.largestrait.toString();
      case 13:
          return player.score.fullhouse == -1 ? ' ' : player.score.fullhouse.toString();
      case 14:
          return player.score.chance == -1 ? ' ' : player.score.chance.toString();
      case 15:
          return player.score.yatzy == -1 ? ' ' : player.score.yatzy.toString();
      }
    },

    getBonus(player) {
       let sum = 
         player.score.ones +
         player.score.twos +
         player.score.threes +
         player.score.fours +
         player.score.fives +
         player.score.sixes;
         if(sum >= 63)
           return 50;
         else
           return 0
    },

    getMinorSum(player) {
       return (player.score.ones == -1 ? 0 : player.score.ones) +
       (player.score.twos == -1 ? 0 : player.score.twos) +
       (player.score.threes == -1 ? 0 : player.score.threes) +
       (player.score.fours == -1 ? 0 : player.score.fours) +
       (player.score.fives == -1 ? 0 : player.score.fives) +
       (player.score.sixes == -1 ? 0 : player.score.sixes);
       
         
    },

    getMajorSum(player) {
       return (player.score.onepair == -1 ? 0 : player.score.onepair) +
       (player.score.twopair == -1 ? 0 : player.score.twopair) +
       (player.score.tripple == -1 ? 0 : player.score.tripple) +
       (player.score.quadruple == -1 ? 0 : player.score.quadruple) +
       (player.score.smallstrait == -1 ? 0 : player.score.smallstrait) +
       (player.score.largestrait == -1 ? 0 : player.score.largestrait) +
       (player.score.fullhouse == -1 ? 0 : player.score.fullhouse) +
       (player.score.chance == -1 ? 0 : player.score.chance) +
       (player.score.yatzy == -1 ? 0 : player.score.yatzy);         
    },

    getFullSum(player) {
      return this.getMinorSum(player) + this.getBonus(player) + this.getMajorSum(player);
    },

    markScore(player,markAs) {      
      if(player.rollsLeft < 3)
        this.$emit('markScore', {markAs: markAs});
    }
  }
}
</script>

